*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
  user-select: none;
  font-family: "poppins","lato",system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
body{
  background-color: #f1f1f1;
  padding-bottom: 75px;
}
.main-inner-wrapper img{
  width: 50%;
}
.header-wrapper{
  border: 1px solid black;
  position: sticky;
  top: 0;
  display: grid;
  z-index: 2;
  grid-template-columns: 20% auto 20%;
  background-color: black;
  color: white;
}
.header-wrapper div{
  text-align: center;
  display: flex;
  flex-flow: wrap row;
  justify-content: center;
  align-content: center;
}
.header-wrapper div button{
  display: block;
  margin: auto;
  border: 0;
  outline: 0;   
  padding: 4px 12px;
  font-size: 16px;
  background-color: white;
  border-radius: 10px;
}
.header-wrapper div button:hover{
  transition: 0.3s;
  background-color: #ffffffe3;
}
.header-wrapper h3{
  font-size: 22px;
  padding: 10px 0;
}

.login-wrapper{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  width: 88%;
  padding: 30px 0 20px;
  box-shadow:-10px -10px 20px #fff,8px 8px 12px #aeaec0;
}
.login-wrapper input{
  display: block;
  margin: 0 auto 18px;
  padding:12px 18px;
  width: 85%;
  font-size: 18px;
  box-shadow:inset -5px -5px 12px #fff,inset 5px 5px 12px #aeaec0;
  border: 0;
  outline: 0;
  border-radius: 14px;
}
.login-wrapper .show{
  margin: 10px 10px 0;
  text-align: center;
  color: red;
  font-size: 16px;
}
.login-wrapper .hide{
  display: none;
}
.login-wrapper button{
  display: block;
  margin: auto;
  text-decoration: none;
  border-radius: 7px;
  font-size:20px;
  color: black;
  padding: 2px 12px;
  box-shadow:-5px -5px 15px #fff,5px 5px 6px #aeaec0;
  border: 0;
  outline: 0;
}
.login-wrapper button:hover{
  color: darkblue;
  transition: 0.3s;   
  box-shadow:inset -3px -3px 6px #fff,inset 3px 3px 6px #aeaec0;
}



.footer-wrapper{
  width: 100%;
  display: grid;
  grid-template-columns: 20% auto 20%;
  list-style-type: none;
  background-color: black;
  position:fixed;
  bottom: 0;
  padding: 14px 0;
}
.footer-wrapper li{
  margin: auto;
  background-color: white;
  padding: 4px 0px;
  border-radius: 10px;
}
.footer-wrapper li .aa{
  font-size: 17px;
  padding: 4px 12px;
  color: black;
}
.footer-wrapper li .a{
  color: black;
  padding: 4px 12px;
  font-size:17px;
}
.footer-wrapper li:hover{
  transition: 0.3s;
  background-color: #ffffffe3;
}
.contact-wrapper .contact-inner-header{
  width: 92%;
  display: flex;
  flex-flow: wrap row;
  margin: 18px auto 20px;
  box-shadow:inset -5px -5px 12px #fff,inset 5px 5px 10px #aeaec0;
  padding: 20px;
}
.contact-inner-header img{
  width: 100%;
  padding: 10px;
  margin-bottom: 20px;
  box-shadow:-5px -5px 14px #fff,5px 5px 8px #aeaec0;
}
.contact-inner-header img:hover{
  box-shadow:inset -5px -5px 12px #fff,inset 5px 5px 10px #aeaec0;
  transition: 0.3s;
}
.contact-wrapper .contact-inner-social{
  width: 100%;
  margin: auto;
  display: flex;
  flex-flow: wrap row;
  justify-content: space-around;
}
.contact-wrapper .embed-inner-wrapper{
  margin: 20px auto;
  width: 95%;
  padding: 20px;
  box-shadow:inset -5px -5px 12px #fff,inset 5px 5px 10px #aeaec0;
}
.contact-inner-social .i{
  box-shadow:-4px -4px 14px #fff,4px 4px 6px #aeaec0;
  font-size: 20px;
  padding: 8px 12px;
  color: black;
  border-radius: 6px;
}
.contact-inner-social .i:hover{
  color: darkblue;
  transition: 0.3s;
  box-shadow:inset -4px -4px 6px #fff,inset 4px 4px 6px #aeaec0;
}
.contact-inner-header .contact-call{
  display: flex;
  flex-flow: wrap row;
  justify-content: space-between;
  width: 100%;
}
.contact-call a{
  display: flex;
}
.contact-call .i{
  align-self: center;
  box-shadow:-3px -3px 8px #fff,3px 3px 6px #aeaec0;
  font-size: 18px;
  padding: 8px 12px;
  color: black;
  border-radius: 6px;
}
.contact-call .i:hover{
  transition: 0.3s;
  box-shadow:inset -3px -3px 6px #fff,inset 3px 3px 6px #aeaec0;
  color: darkblue;
}
.contact-inner-header h4{ 
  box-shadow:-3px -3px 8px #fff,3px 3px 6px #aeaec0;
  padding: 5px 12px;
  color: black;
  font-size: 18px;
  border-radius: 5px;
}
.contact-inner-header h4:hover{
  box-shadow:inset -3px -3px 6px #fff,inset 3px 3px 6px #aeaec0;
  transition: 0.3s;
  color: red;
}
.contact-footer-wrapper{
  display: flex;  
  width: 100%;
  margin: auto;
  padding-bottom: 15px;
  flex-flow: wrap row;
  justify-content: space-evenly;
}
.contact-footer-wrapper h6{
  width: 100%;
  text-align: center;
  font-size: 19px;
  color: red;
  padding:0 0 20px 0;
}
.contact-footer-wrapper div{
  padding: 8px 0;
  width: 45%;
  margin: 0;
  box-shadow:-4px -4px 18px #fff,4px 4px 6px #aeaec0;
  color: black;
  border-radius: 12px;
  font-weight: bold;
}
.contact-footer-wrapper .contact-footer-left{
  width: 38%;
}
.contact-footer-wrapper .contact-footer-right{
  width: 50%;
}
.contact-footer-wrapper div p{
  font-size: 14px;
  text-align: center;
  margin: 5px 0;
}
.contact-wrapper .embed-inner-wrapper{
  margin: 20px auto 15px;
  width: 95%;
  padding: 22px;
  box-shadow:inset -4px -4px 10px #fff,inset 5px 5px 8px #aeaec0;
}
.embed-inner-wrapper iframe{
  filter: brightness(103%);
  border-radius:20px;
  border: 0;
  padding: 10px;
  box-shadow:inset -3px -3px 6px #fff,inset 3px 3px 6px #aeaec0;
  width: 100%;
  height: 150px;
  margin: auto;
  display: block;
}
.embed-inner-wrapper h5{
  text-align: center;
  box-shadow:-4px -4px 8px #fff,4px 4px 8px #aeaec0;
  border-radius: 20px;
  color: black;
  font-size: 15px;
  line-height: 26px;
  padding: 16px 5px;
}
.contact-wrapper{
  margin-bottom: 80px;
}
@media screen and (max-width:338px){
  .embed-inner-wrapper h5{
    font-size: 14px;
  }  
  .contact-inner-social .i{
    padding: 6px 9px;
    font-size: 19px;
  }
  .contact-footer-wrapper div p{
    font-size: 13px;
  }
}
@media screen and (max-width:311px){
  .contact-footer-wrapper div p{
    font-size: 13px;
  }  
  .embed-inner-wrapper h5{
    font-size: 13px;
    line-height: 24px;
  }
  .contact-footer-wrapper .contact-footer-left{
    width: 35%;
  }
  .contact-footer-wrapper{
    justify-content: space-around;
  }
}

.splash-loader{
  width: 85%;
  border-radius: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}
.splash-loader img{
  width: 100%;
  transform: scale(1,1);
  animation-name: scale;
  animation-duration: 2s;
  animation-timing-function: ease-in;
  animation-iteration-count:1;
}

.home-header-wrapper{
  width: 100%;
  display: flex;
  flex-flow: wrap row;
  margin: 0px auto;
  justify-content: center;
  align-items: center;
  position: relative;
}
.home-header-wrapper img{
  width: 100%;
  filter: brightness(80%);
}
.home-header-wrapper p{
  text-align: center;
  width: 90%;
  font-family: "Dancing Script",serif;
  position: absolute;
  font-weight: bold;
  color: white;
  font-size: 26px;
  line-height: 38px;
}
@keyframes scale{
  from{
    transform: scale(0.4,0.4);
  } 
  to{
    transform: scale(1,1);
  } 
}

.home-comment-wrapper{
  width: 100%;
  display: grid;
  grid-template-columns: 100% 100% 100%;
  overflow: hidden;
}
.home-comment-wrapper .home-inner-comment{
  margin: 0 10px 10px;
  background-color: black;
  color: white;
  border-radius: 15px;
  padding: 20px 22px 25px;
  position: relative;
  left: 0%;
  animation-name: cmt;
  animation-duration: 20s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}
@keyframes cmt{
  0%{
    left: 0%;
  }  
  17%{
    left: 0%;
  }
  19%{
    left: -100%;
  }
  44%{
    left: -100%;
  }
  46%{
    left: -200%;
  }
  71%{
    left: -200%;
  }
  73%{
    left: -100%;
  }
  98%{
    left: -100%;
  }
  100%{
    left: 0%;
  }
}
.home-inner-comment h3{
  text-align: center;
  margin:0 0 10px 0;
}
.home-inner-comment p{
  font-family:"Lato";
  font-size: 15px;
  line-height: 23px;
  text-align: justify;
}

.home-section-wrapper{
  width: 92%;
  margin: 15px auto 25px;
  box-shadow:-4px -4px 8px #fff,4px 4px 6px #aeaec0;
  padding:15px;
}
.home-section-wrapper .home-inner-section{
  position: relative;
  display: flex;
  flex-flow: wrap row;
  justify-content: center;
  align-items: center;
}
.home-inner-section h4{
  position: absolute;
  color: white;
  padding: 15px;
  text-align: center;
  font-size: 25px;
}
.home-heading{
  position: sticky;
  top: 55px;
  background-color: #f1f1f1;
  z-index: 2;
  padding: 10px 0;
  margin-top: 0px;
}
.home-section-wrapper p{
  margin-top: 15px;
  color: black;
  padding: 15px 20px;
  font-size: 14px;
  line-height: 22px;
  text-align: justify;
  box-shadow:-3px -3px 10px #fff,3px 3px 8px #aeaec0;
}
.home-section-wrapper p:hover{
  transition: 0.3s;
  box-shadow:inset -3px -3px 8px #fff,inset 3px 3px 8px #aeaec0;
  color: black;
}
.home-inner-section img{
  box-shadow:inset -3px -3px 8px #fff,inset 3px 3px 8px #aeaec0;
  padding: 15px;
  width: 100%;
  filter: brightness(70%);
}
.home-inner-section img:hover{
  transition: 0.3s;
  filter: brightness(95%);
}


.upload-wrapper .upload-inner-wrapper{
  width: 100%;
  margin:20px auto; 
  padding: 22px 0px;
  box-shadow:inset -5px -5px 12px #fff,inset 5px 5px 10px #aeaec0;
  border-radius: 20px;
}
.upload-inner-wrapper input{
  display: block;
  box-shadow:inset -5px -5px 12px #fff,inset 5px 5px 10px #aeaec0;
  margin: 0px auto;
  border-radius: 14px;
  font-size: 18px;
  width: 88%;
  padding: 12px 18px 10px;
  border: 0;
  outline: 0;
}
.upload-inner-wrapper input[type="file"]::-webkit-file-upload-button{
  border: 0;
  outline: 0;
  margin: 12px 12px 12px 10px;
  color: black;
  font-weight: bold;
  font-size: 16px;
  padding: 6px 12px;
  border-radius: 8px;
  box-shadow:-3px -3px 8px #fff,3px 3px 6px #aeaec0;
}
.upload-inner-wrapper input[type="file"]::-webkit-file-upload-button:hover{
  box-shadow:inset -3px -3px 6px #fff,inset 3px 3px 6px #aeaec0;
  color: red;
}
.upload-inner-wrapper button{
  text-decoration: none;
  border-radius: 7px;
  font-size:20px;
  color: black;
  padding: 2px 9px;
  box-shadow:-3px -3px 8px #fff,3px 3px 6px #aeaec0;
  border: 0;
  outline: 0;
  display: block;
  margin:auto 0 auto;
}
.upload-inner-wrapper .upload-inner-box{
  display: flex;
  flex-flow: wrap row;
  justify-content: space-between;
  align-items: center;
  width: 88%;
  margin: 15px auto 0;
}
.upload-inner-box #bg_btn{
  margin: 0;
}
.upload-inner-box #bg_url{
  width: 83%;
  margin: 0;
  border-radius: 16px;
  padding: 5px 8px;
  font-size: 12px;
}
.upload-inner-wrapper button:hover{
  color: red;   
  box-shadow:inset -3px -3px 6px #fff,inset 3px 3px 6px #aeaec0;
  transition: 0.3s;
}


.album-img-wrapper{
  width: 92%;
  display: grid;
  grid-template-columns: 90%;
  justify-content: space-evenly;
  box-shadow:inset -5px -5px 12px #fff,inset 5px 5px 10px #aeaec0;
  margin: auto;
  padding: 20px 0 0;
}
.album-img-wrapper .album-img-inner-wrapper{
  display: flex;
  margin-bottom: 20px;
  box-shadow:-3px -3px 8px #fff,3px 3px 6px #aeaec0;
  align-items: center;
  position: relative;
}
.album-img-inner-wrapper img{
  padding: 6px;
  background-color: black;
  display: block;
  filter: brightness(80%);
  margin: auto;
  overflow: hidden;
}
.album-img-inner-wrapper .Xmark{
  position: absolute;
  bottom: 0px;
  right: 0px;
  padding: 8px;
  background-color: black;
  border-radius: 6px;
  font-size: 19px;
  color: white;
}
.album-img-inner-wrapper .Xmark:hover{
  transition: 0.3s;
  color: red;
}
.main-wrapper{
  width: 100%;
  display: grid;
  grid-template-columns: 90%;
  justify-content: center;
}
.main-wrapper .main-inner-wrapper{
  margin-top: 20px;
  padding: 12px;
  position: relative;
  display: flex;
  flex-flow: wrap row;
  justify-content: center;
  box-shadow:-5px -5px 14px #fff,5px 5px 8px #aeaec0;
  align-items:center;
}
.main-inner-wrapper h4{
  font-size: 24px;
  position: absolute;
  color: white;
  padding: 0 30px;
  text-align: center;
  font-weight: 500;
}
.main-inner-wrapper img{
  width: 100%;
  filter: brightness(60%);
}
.main-inner-wrapper img:hover{
  transition: 0.3s;
  filter: brightness(80%);
}
.main-inner-wrapper .Xmark{
  position: absolute;
  bottom: 0px;
  right: 0px;
  padding: 6px;
  font-size: 20px;
  color: black;
  background-color: #f1f1f1;
  border-radius: 5px;
}

.section-wrapper .album-title-wrapper{
  background-color: darkblue;
  width: 96%;
  border: 1px solid black;
  border-radius: 5px;
  margin: 8px auto;
}
.album-title-wrapper h4{
  margin: 15px auto;
  width: 94%;
  font-size: 20px;
  color:black;
  border-radius: 0px;
  box-shadow:-3px -3px 8px #fff,3px 3px 6px #aeaec0;
  text-align: center;
  padding: 8px 0;  
}

